import { Routes, Route } from 'react-router-dom';
import Layout from './Layout';
import RegistrationPage from './RegistrationPage/RegistrationPage';
import DashboardPage from './DashboardPage/DashboardPage';
export const App = () => {
  return (
    <div>
      <Routes>
        <Route path="/" element={<Layout />}>
          {/* Przy ciekach rejestracji i loginu bed restrictedRoutu przy zalogowanym uzytkoniku bed przenoci na dashboard */}
          <Route path="registration" element={<RegistrationPage />} />
          <Route path="login" element={<RegistrationPage />} />
          {/* Poniej priv route ktry bedzie przenosi do loginu/rejestracji gdy uytkownik nie bedzie zalogowany */}
          <Route path="dashboard" element={DashboardPage} />
        </Route>
      </Routes>
    </div>
  );
};
